img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  margin: 0 auto;
  width: 1150px;
}

.page {
  height: 100%;
}

.page__body {
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  margin: 0 auto;
  font-family: "Gothic", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 30px;
}

@font-face {
  font-family: "Gothic";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/GothicA1-Regular.woff2") format("woff2"), url("../fonts/GothicA1-Regular.woff2");
}
@font-face {
  font-family: "Gothic";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/GothicA1-Bold.woff2") format("woff2"), url("../fonts/GothicA1-Bold.woff");
}
:root {
  --bg-header: linear-gradient(287.15deg, #450E4B 0%, #3C0C41 24.48%, rgba(207, 0, 99, 0) 100%), #CF0063;
  --color-white: #FFFFFF;
  --color-header-hover: #CF0063;
  --color-button-hover: #3C0C41;
  --easy-pink: #FC0A7E;
}

